body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body{
  padding:0;
  margin:0;
}

body{
  background-color: #3498DB;
}
.calc{
    text-align: center;
    max-width: 850px;
    margin: 0 auto;
    height: 100vh;
}
.calc__buttons{
    display: grid;
    grid-template-columns: 75% 25%;
    height: 100%;
}
.calc__numbers{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 80%;
}
.calc__operators{
    display: flex;
    flex-direction: column;
    
}
.calc__operators .calc__key-wrapper{
    display: flex; 
    justify-content: center;
    align-items: center;   
}
.calc__key{
    border-radius: 60px;
    font-size: 55px;
    line-height: 1em;
    width: 100px;
    height: 100px;
    background-color: transparent;
    color: #00FFCC;
    border:2px solid #00FFCC;
    cursor: pointer;
    -webkit-transition: all .1s linear;
    transition: all .1s linear;
}
.calc__key-wrapper{
    flex-basis: 33%;
}
.calc__key:hover{
    background-color: #CCFF99;
    color: #3498DB;
    border-color: #3498DB;  
}
.calc__display{
    position: absolute;
    width: 100%;
    font-size: 300px;
    border: none;
    background-color: #3498DB;
    color: #33FF99;  
    bottom: 0;
    left: 0;
    z-index: -1;
    box-sizing: border-box;
    padding:0 20px;
    font-family: monospace;
}
.calc__key-wrapper--equals{
    display: flex;
    justify-content: center;
    align-items: center;
}
